:root {
    --primary-background: #ffffff;
    --secondary-background: #fcfcfc;
    --primary-text: #020202;
    --secondary-text: #777777;
    --accent: #83eecc;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    font-family: "Mulish", sans-serif;
    background-color: var(--primary-background);
    color: var(--primary-text);
    font-weight: 500;
}

.theme-toggle:checked {
    --primary-background: #000000;
    --secondary-background: #fcfcfc;
    --primary-text: #020202;
    --secondary-text: #777777;
    --accent: #83eecc;
}

.container {
    background-color: var(--primary-background);
    padding: 2em 4em;
}

.container-alt {
    background-color: var(--secondary-background);
}

.right-clip-1,
.right-clip-2 {
    position: absolute;
    top: 0;
    right: 0;
    clip-path: polygon(100% 0, 0 0, 100% 100%);
}

.right-clip-1 {
    z-index: 1;
    background-color: #f5f5f5;
    width: 200px;
    height: 200px;
}
.right-clip-2 {
    background-color: #fbfbfb;
    width: 270px;
    height: 270px;
}

.flex-c-spbw {
    min-height: calc(100vh - 4em);
    padding: 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.logo {
    font-size: 1.6em;
    width: min-content;
    font-weight: 800;
    margin-top: 1em;
}

.social-icons {
    display: flex;
    align-items: center;
    margin-bottom: 3em;
}

.h-line {
    width: 200px;
    height: 2px;
    background-color: var(--secondary-text);
    margin-right: 2em;
}

.icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 186px;
}

.icons i {
    font-size: 1.6em;
    color: var(--secondary-text);
    transition: all 0.2s ease;
}

.fa-instagram:hover {
    color: #c13584;
}

.fa-github:hover {
    color: #333333;
}

.fa-twitter:hover {
    color: #1da1f2;
}

.fa-dev:hover {
    color: #0a0a0a;
}

.text-cont {
    display: flex;
    align-items: center;
}

.vert-btn {
    width: max-content;
    transform: rotate(-90deg) translate(8px, -70px);
    font-size: 1.3em;
    padding: 4px 14px;
    color: var(--primary-text);
    font-weight: 700;
    text-decoration: none;
    padding: 6px 20px 10px 20px;
    transition: all 0.2s ease;
    background-color: var(--accent);
    border: 2px solid var(--accent);
    border-radius: 36px;
}

.vert-btn:hover {
    background-color: transparent;
}

.landing-text {
    font-size: 3em;
    font-weight: 700;
    line-height: 1.5;
    padding-bottom: 1.5rem;
    transform: translateX(-80px);
}

.landing-text span {
    color: var(--accent);
}

.flex-r-spbw {
    padding: 3em 1em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

.aboout-text {
    width: 60%;
}

.about-heading {
    font-size: 2em;
    font-weight: 700;
    margin-bottom: 1em;
}

.about-me {
    color: var(--secondary-text);
    letter-spacing: 0.4px;
    line-height: 2;
}

.about-icons {
    position: absolute;
    right: calc(1em + 246px);
    background-color: var(--secondary-background);
    border: 1px solid #ccc;
    width: 96px;
    padding: 0.5em 1em;
    margin-bottom: 1.4em;
    border-radius: 32px;
    box-shadow: -1px 1px 4px 1px rgba(0, 0, 0, 0.15);
}

.about-icons i {
    font-size: 1.5em;
}

.about-icons .fa-dev {
    color: #35c9a4;
}

.about-icons .fa-github {
    color: #5c6bc0;
}

.about-icons .fa-dev:hover {
    color: #f38e8d;
}

.about-icons .fa-github:hover {
    color: #ddac23;
}

.banner {
    position: relative;
    width: 230px;
    height: 230px;
    border: 1px solid #ccc;
    border-radius: 12px;
}

.skills-cont {
    padding: 3em 1em;
}

.skills-grid {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2em;
    margin-top: 4em;
}

.skill-item {
    font-weight: 700;
    font-size: 1.1em;
    color: var(--primary-text);
    list-style-type: circle;
}

.project-grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5em 7em;
    margin-top: 4em;
}

.project-grid a {
    text-decoration: none;
    color: inherit;
}

.project-img {
    width: 100%;
    height: 300px;
    background-color: bisque;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 6px;
    margin-bottom: 2em;
    transition: all 0.2s ease;
}

.project-img:hover {
    transform: scale(1.03);
}

.project-heading {
    font-size: 1.8em;
    margin-bottom: 0.5em;
}

.project-details {
    line-height: 1.6;
}

.view-href {
    width: max-content;
    margin-top: 1.5em;
    font-size: 1.05em;
    font-weight: 700;
}

.fa-angle-right {
    transform: translate(2px, 1px);
    transition: all 0.2s ease;
}

.view-more {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 2em 5.5em;
}

.view-more > div {
    display: flex;
    align-items: center;
}

.view-more p {
    color: var(--primary-text);
    font-size: 1.4em;
    font-weight: 700;
    line-height: 1.7;
    margin-bottom: 2em;
}

.view-more a {
    background-color: #eee;
    color: rgba(0, 0, 0, 0.9);
    font-weight: 600;
    border-radius: 40px;
    padding: 0.5em 1.1em;
    font-size: 1.05em;
    transition: all 0.2s ease;
}

.blog-link {
    margin-left: 1.2em;
}

.repo-link:hover {
    background-color: rgba(53, 201, 164, 0.2);
}
.blog-link:hover {
    background-color: rgba(123, 120, 255, 0.25);
}

.fa-caret-right {
    margin-left: 0.1em;
}

.view-href:hover .fa-angle-right,
.cool-text:hover .fa-angle-right {
    transform: translate(8px, 1px);
}

.contact-cont {
    padding: 3em 1em;
}

.mail-link {
    width: min-content;
    margin-top: 4em;
    display: flex;
    align-items: center;
}

.fa-envelope {
    transform: translateY(3px);
    margin-right: 12px;
    font-size: 1.25em;
}

.mail-link-href {
    color: var(--primary-text);
    font-weight: 700;
    text-decoration: none;
}

.mail-link-href::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    margin-top: 2px;
    background-color: var(--primary-text);
}

.cool-insta-cont {
    margin-top: 4em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cool-text p {
    width: 200px;
    font-size: 1.5em;
    font-weight: 700;
    margin-bottom: 1em;
}

.cool-text a {
    color: var(--primary-text);
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
}

.insta-grid {
    display: grid;
    grid-template-areas:
        "feed1 feed1 feed2 feed4 feed4"
        "feed1 feed1 feed3 feed4 feed4";
    gap: 20px;
}

.insta-grid img {
    border-radius: 6px;
}

.insta-feed-1 {
    grid-area: feed1;
    width: 300px;
    height: 300px;
}
.insta-feed-2 {
    grid-area: feed2;
    width: 140px;
    height: 140px;
}
.insta-feed-3 {
    grid-area: feed3;
    width: 140px;
    height: 140px;
}
.insta-feed-4 {
    grid-area: feed4;
    width: 300px;
    height: 300px;
}

.footer-cont {
    padding: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 1em;
}

.footer-logo {
    margin: 0;
}

.footer-cont p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--primary-text);
    color: var(--accent);
    padding: 0.3em 0.6em;
    border-radius: 4px;
    font-size: 0.9em;
}

.footer-links {
    display: flex;
    align-items: center;
}

.footer-links a {
    margin-left: 1em;
    color: var(--primary-text);
    text-decoration: none;
    transition: all 0.2s ease;
}

.insta-link:hover {
    color: #c13584;
}

.github-link:hover {
    color: #333333;
}

.dev-link:hover {
    color: #0a0a0a;
}

.twitter-link:hover {
    color: #1da1f2;
}

.hide-lg {
    display: none;
}

@media only screen and (max-width: 1200px) {
    .landing-text {
        margin-left: 1.5em;
    }
    .hide-md {
        display: none;
    }
    .horz-btn {
        display: block;
        width: fit-content;
        margin-top: 1.4em;
        font-size: 0.4em;
        padding: 4px 14px;
        color: var(--primary-text);
        font-weight: 700;
        text-decoration: none;
        transition: all 0.2s ease;
        background-color: var(--accent);
        border: 2px solid var(--accent);
        border-radius: 20px;
    }

    .horz-btn:hover {
        background-color: transparent;
    }

    .view-more {
        padding: 2em 0;
    }

    .insta-feed-1 {
        width: 260px;
        height: 260px;
    }
    .insta-feed-2 {
        width: 120px;
        height: 120px;
    }
    .insta-feed-3 {
        width: 120px;
        height: 120px;
    }
    .insta-feed-4 {
        grid-area: feed4;
        width: 260px;
        height: 260px;
    }
}

@media only screen and (max-width: 1000px) {
    .container {
        padding: 1.8em 3em;
    }

    .flex-r-spbw {
        align-items: flex-start;
    }

    .w-full-md {
        width: 86vw;
    }

    .skills-grid {
        width: 85%;
        grid-template-columns: repeat(3, 1fr);
    }

    .project-grid {
        gap: 4em;
    }

    .project-img {
        height: 200px;
    }

    .view-more {
        padding: 2em 1em;
    }
    .view-more > div {
        flex-direction: column;
        align-items: center;
    }

    .blog-link {
        margin-top: 1.2em;
        margin-left: 0;
    }

    .insta-feed-1 {
        width: 200px;
        height: 200px;
    }
    .insta-feed-2 {
        width: 90px;
        height: 90px;
    }
    .insta-feed-3 {
        width: 90px;
        height: 90px;
    }
    .insta-feed-4 {
        grid-area: feed4;
        width: 200px;
        height: 200px;
    }
}

@media only screen and (max-width: 760px) {
    .container {
        padding: 1.6em 2em;
    }

    .landing-text {
        font-size: 2.4em;
        margin-left: 2em;
    }

    .horz-btn {
        font-size: 0.5em;
        padding: 4px 18px;
    }

    .h-line {
        width: 140px;
        margin-right: 1.2em;
    }

    .right-clip-1 {
        width: 140px;
        height: 140px;
    }
    .right-clip-2 {
        width: 200px;
        height: 200px;
    }

    .banner {
        width: 200px;
        height: 200px;
    }

    .skills-grid {
        width: 74%;
        grid-template-columns: 1fr 1fr;
    }

    .skill-item {
        font-size: 1.2em;
    }

    .project-grid {
        grid-template-columns: 1fr;
        gap: 5em;
    }

    .project-img {
        height: 340px;
    }

    .view-more {
        padding: 2em 5.5em;
    }

    .view-more > div {
        flex-direction: row;
        align-items: center;
    }

    .blog-link {
        margin-top: 0;
        margin-left: 1.2em;
    }

    .cool-insta-cont {
        flex-direction: column-reverse;
        align-items: flex-start;
    }

    .insta-grid {
        margin-bottom: 2.4em;
    }

    .insta-feed-1 {
        width: 240px;
        height: 240px;
    }
    .insta-feed-2 {
        width: 110px;
        height: 110px;
    }
    .insta-feed-3 {
        width: 110px;
        height: 110px;
    }
    .insta-feed-4 {
        width: 240px;
        height: 240px;
    }

    .cool-text p {
        width: 100%;
        font-size: 1.7em;
        margin-bottom: 0.6em;
    }
}

@media only screen and (max-width: 540px) {
    .container {
        padding: 1em 1.4em;
    }

    .logo {
        font-size: 1.4em;
    }

    .flex-c-spbw {
        min-height: calc(92vh - 4em);
    }

    .social-icons {
        margin-bottom: 2em;
    }

    .landing-text {
        font-size: 1.7em;
        margin-left: 2.9em;
    }

    .horz-btn {
        font-size: 0.55em;
    }

    .right-clip-1 {
        width: 80px;
        height: 80px;
    }
    .right-clip-2 {
        width: 140px;
        height: 140px;
    }

    .view-more {
        padding: 2em 1em;
    }

    .flex-r-spbw {
        flex-direction: column;
    }

    .hide-sm {
        display: none;
    }

    .aboout-text {
        width: 100%;
    }

    .w-full-md {
        width: 82vw;
    }

    .skills-grid {
        width: 100%;
    }

    .skill-item {
        font-size: 1em;
    }

    .project-img {
        height: 240px;
    }

    .insta-grid {
        gap: 14px;
    }

    .insta-feed-1 {
        width: 240px;
        height: 240px;
    }
    .insta-feed-2 {
        width: 110px;
        height: 110px;
    }
    .insta-feed-3 {
        width: 110px;
        height: 110px;
    }
    .insta-feed-4 {
        display: none;
    }
}

@media only screen and (max-width: 440px) {
    .container {
        padding: 0.6em;
    }
    .landing-text {
        font-size: 1.8em;
        margin-left: 2.85em;
    }

    .view-more {
        padding: 2em 0.3em;
    }
    .view-more > div {
        flex-direction: column;
        align-items: center;
    }

    .blog-link {
        margin-top: 1.2em;
        margin-left: 0;
    }

    .h-line {
        width: 100px;
    }

    .project-img {
        height: 186px;
    }

    .insta-feed-1 {
        width: 200px;
        height: 200px;
    }
    .insta-feed-2 {
        width: 90px;
        height: 90px;
    }
    .insta-feed-3 {
        width: 90px;
        height: 90px;
    }
}

